import { Inject, Injectable, InjectionToken, ModuleWithProviders, NgModule, APP_INITIALIZER } from '@angular/core';
import { CommunicationDefinition, serializerDeserializer } from '@backbase/communication-property';
import { Observable } from 'rxjs';
import { first, map } from 'rxjs/operators';

import {
  BackbaseCoreModule,
  PipelineSink,
  Sink,
  PipelineProcessorConfig,
  ӨItemNavigationService,
  ӨRootContainerService,
} from '@backbase/foundation-ang/core';

export function initIntents(intentBridgeService: IntentBridgeService) {
  return intentBridgeService.handleIncomingIntent.bind(intentBridgeService);
}

/**
 * @deprecated Will be removed in v7.0.0
 * A WA2 compatible Intent
 *
 * This is the interoperable format that is supported by both sides of the communication
 */
export interface Intent<T extends object = object> {
  /** The name of the Intent used to identify it */
  name: string;
  /** Any extra data associated with the intent that can be used by the receiving side */
  parameters: T;
}

@Injectable()
export class IntentNavigation {
  go(url: string) {
    location.href = url;
  }
}

@Injectable()
export class IntentStorage {
  static STORE_KEY = 'lib-bb-intent:navigated-intent';

  get(): Intent | undefined {
    const storedIntent: string | undefined = sessionStorage.getItem(IntentStorage.STORE_KEY) || undefined;
    if (storedIntent === undefined) {
      return;
    }
    const intent: unknown = JSON.parse(storedIntent);
    if (!isAnIntent(intent)) {
      return;
    }
    return intent;
  }

  clear(): void {
    sessionStorage.removeItem(IntentStorage.STORE_KEY);
  }

  set(intent: Intent): void {
    sessionStorage.setItem(IntentStorage.STORE_KEY, JSON.stringify(intent));
  }
}

// Incoming

function isAnIntent(intent: unknown): intent is Intent {
  if (typeof intent !== 'object' || intent === null) {
    return false;
  }
  const assumedIntent = intent as Intent;
  const hasName = typeof assumedIntent.name === 'string';
  const hasParameters = typeof assumedIntent.parameters === 'object' && assumedIntent.parameters !== null;
  return hasName && hasParameters;
}

function intentHandler<T extends Intent>(
  guard: (intent: Intent) => intent is T,
  handle: (t: T['parameters']) => Promise<void>,
): (intent: Intent) => undefined | Promise<void> {
  return intent => {
    if (!guard(intent)) {
      return undefined;
    }
    return handle(intent.parameters);
  };
}

/**
 * @deprecated Will be removed in v7.0.0
 * Describe the mapping of an incoming Intent to a RouteParam communication
 */
export interface IncomingIntentMapping<I extends Intent> {
  /**
   * Determine if the incoming intent can be handled by this mapper
   *
   * This will likely involve checking the name of the intent and that any required parameters were sent
   */
  guard: (intent: I) => intent is I;
  /**
   * Extract the value from the Intents parameters to be passed to the RouteParam communication group
   */
  extract: (parameters: I['parameters']) => string;
  /**
   * The name of the communication group that is the target of the resulting RouteParam communication
   */
  group: string;
}

export const INTENT_INCOMING_MAPPING = new InjectionToken('INTENT_INCOMING_MAPPING');

@Injectable()
export class IntentBridgeService {
  constructor(
    private readonly rootModel: ӨRootContainerService,
    private readonly itemNavigation: ӨItemNavigationService,
    private readonly store: IntentStorage,
    @Inject(INTENT_INCOMING_MAPPING) private readonly handlers: Array<IncomingIntentMapping<Intent<any>>>,
  ) {}

  private handleIntentFromStorage(handlers: Array<(intent: Intent) => undefined | Promise<void>>) {
    const intent = this.store.get();
    if (!intent) {
      return;
    }

    for (const handle of handlers) {
      const handling = handle(intent);
      if (handling) {
        this.store.clear();
        return handling;
      }
    }
    return;
  }

  handleIncomingIntent(): void {
    this.handleIntentFromStorage(
      this.handlers.map(({ guard, group, extract }) =>
        intentHandler(guard, parameters => {
          const params = { [group]: extract(parameters) };
          return this.rootModel
            .commonAncestor(new CommunicationDefinition('route-param', group))
            .pipe(
              first(),
              map(commonAncestor => {
                if (commonAncestor === undefined) {
                  return console.warn('No Inputs configured to receive route param');
                }
                this.itemNavigation.navigateToItem(commonAncestor.name, params);
              }),
            )
            .toPromise();
        }),
      ),
    );
  }
}

// Outgoing

const TAG = 'intent';

/**
 * @deprecated Will be removed in v7.0.0
 * Intent Communication Transport
 *
 * Can be used in standalone development to configure communication using Intents.
 *
 * Note: This Transport does not currently support Input handling. Incoming Intents can be handled
 * by mapping them to a RouteParam by providing an `IncomingIntentMapping`
 */
export class IntentIO {
  /**
   * Generate a serialized communication configuration suitable for use as a Widget Output property value.
   *
   * @param group The name of the communication group to which the Output belongs
   */
  static toProperty(group: string): string {
    return serializerDeserializer.toProperty(new CommunicationDefinition(TAG, group));
  }
}

export const INTENTS_OUTGOING_MAPPING = new InjectionToken('OutgoingIntentMapping');

/**
 * @deprecated Will be removed in v7.0.0
 * Describe the mapping of an Output to an Intent
 */
export interface OutgoingIntentMapping<Output, I extends Intent> {
  /** The name of the communication group targeted by the intent output */
  group: string;
  /** The URL on which the target is hosted */
  targetUrl: string;
  /** The name of the intent that will be created from the emitted output value */
  intent: I['name'];
  /** Create the intent parameters based on the emitted output value */
  parameters: (output: Output) => I['parameters'];
}

export class IntentPipeline<Output> implements PipelineSink<Output> {
  constructor(
    private readonly navigator: IntentNavigation,
    private readonly storage: IntentStorage,
    private readonly outgoingMappings: Array<OutgoingIntentMapping<Output, Intent>>,
  ) {}

  sink(group: string): Sink<Output> {
    return (stdin: Observable<Output>): void => {
      const creator = this.outgoingMappings.find(mapping => mapping.group === group);
      if (!creator) {
        return;
      }
      void stdin.subscribe((value: Output) => {
        const parameters = creator.parameters(value);
        const intent: Intent = {
          name: creator.intent,
          parameters,
        };
        this.storage.set(intent);
        this.navigator.go(creator.targetUrl);
      });
    };
  }
}

export function createIntentPipeline<Output>(
  navigator: IntentNavigation,
  storage: IntentStorage,
  creators: Array<OutgoingIntentMapping<Output, Intent>>,
) {
  return new IntentPipeline(navigator, storage, creators);
}

export const intentProcessorConfig: PipelineProcessorConfig<'intent'> = {
  name: TAG,
  provider: {
    provide: new InjectionToken('Intents pipeline handler'),
    useFactory: createIntentPipeline,
    deps: [IntentNavigation, IntentStorage, INTENTS_OUTGOING_MAPPING],
  },
};

/**
 * @deprecated Will be removed in v7.0.0
 */
@NgModule({
  imports: [
    BackbaseCoreModule.withConfig({
      pipelineProcessors: [intentProcessorConfig],
    }),
  ],
})
export class IntentsBridgeModule {
  static forRoot(config: {
    incoming: Array<IncomingIntentMapping<Intent<any>>>;
    outgoing: Array<OutgoingIntentMapping<any, any>>;
  }): ModuleWithProviders<IntentsBridgeModule> {
    return {
      ngModule: IntentsBridgeModule,
      providers: [
        {
          provide: INTENTS_OUTGOING_MAPPING,
          useValue: config.outgoing,
        },
        {
          provide: INTENT_INCOMING_MAPPING,
          useValue: config.incoming,
        },
        IntentBridgeService,
        {
          provide: APP_INITIALIZER,
          useFactory: initIntents,
          deps: [IntentBridgeService],
          multi: true,
        },
        IntentStorage,
        IntentNavigation,
      ],
    };
  }
}
